<template>
  <div>
      <van-nav-bar left-arrow  @click-left="$router.go(-1)" title="分类" />
      <van-sidebar v-model="activeKey"  >
       <van-sidebar-item :title="item.catename" v-for="(item,index) in firstcate" :key="item.id" @click="getCate(index)" />
      </van-sidebar>

<van-grid :border="false" :column-num="3" >
  <van-grid-item v-for="i in secondcate" :key="i.id" :to='`/goodslist?fid=${i.id}`'>
    <van-image :src="$preImg+i.img" />
   <p>{{i.catename}}</p>
  </van-grid-item>
</van-grid>
  </div>
</template>

<script>
import {requestTree} from '../utils/request'
export default {
    data(){
      return{
        activeKey:0,
        firstcate:[],
        secondcate:[]
      }
    },
    methods:{
      gettree(){
        requestTree().then(data=>{
          if(data.code==200){
            this.firstcate=data.list
            this.getCate(0)
          }
        })
      },
      getCate(index){
        this.secondcate=this.firstcate[index].children
        console.log(this.secondcate);
      }
    },
    mounted(){
      this.gettree()
    }
}
</script>

<style>
.van-grid{
  width: calc(100% - 80px);
  position: absolute;
  top: 55px;
  right:  0;
  /* background: pink; */
}
</style>


