import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import {Toast} from 'vant'

import index from '../pages/index.vue'
import layout from '../pages/layout.vue'
import shoppingcar from '../pages/shoppingcar.vue'
import mime from '../pages/mime.vue'
import login from '../pages/login.vue'
import register from '../pages/register.vue'
import cate from '../pages/cate.vue'
import goodslist from '../pages/goodslist.vue'


const router=new Router({
  routes:[
  {
    path: '/',
    component: layout,
    redirect:'/index',
    children: [
      {
        path: '/index',
        name: '首页',
        component: index
      },
      {
        path: '/shoppingcar',
        name: '购物车',
        component: shoppingcar
      },
      {
        path: '/mime',
        name: '我的',
        component: mime
      },
      {
        path: '/cate',
        name: '分类',
        component: cate
      },
      {
        path:'/goodslist',
        name:'商品列表',
        component:goodslist
      },
    ],

  },
  
  {
    path:'/login',
    name:"登录",
    component:login
  },
  {
    path:'/register',
    name:'注册',
    component:register,
  },
]
})

router.beforeEach((to,from,next)=>{
      if(to.path=='/mime'||to.path=='/shoppingcar'){
        const user=JSON.parse(sessionStorage.getItem('data')||'{}')
        if(!user.token){
        Toast.fail('请登录')
        router.push('/login')
          return
        }
      }

      next()
})

export default router



