import axios from 'axios'
import qs from 'qs'

let baseUrl = '/api'
// 请求拦截
axios.interceptors.request.use(req=>{
    console.group('本次请求的路径为：'+req.url)
    const user=JSON.parse(sessionStorage.getItem('data')||'{}')
    if(user.token){
        req.headers.authorization=user.token
    }
    // console.log(req);
    return req
})
// 响应拦截
axios.interceptors.response.use(res => {
    console.group('本次响应的路径为：' + res.config.url);
    console.log(res.data);
    return res.data
})
// 首页轮播图
export const requsetBanner = () => {
    return axios({
        method: 'get',
        url: baseUrl + '/api/getbanner',
    })
}
// 获取首页商品
export const requsestIdexGoods = () => {
    return axios({
        method: 'get',
        url: baseUrl + '/api/getindexgoods',
    })
}
// 获取树形结构
export const requestTree=()=>{
    return axios({
        method:'get',
        url:baseUrl+'/api/getcatetree',
    })
}
// 获取分类商品
export const requestCateGoods=(params)=>{
    return axios({
        method:'get',
        url:baseUrl+'/api/getgoods',
        params:params
    })
}
// 注册账号
export const requestReg=(data)=>{
    return axios({
        method:'post',
        url:baseUrl+'/api/register',
        data:qs.stringify(data)
    })
}
// 登录账号
export const requestlogin=(data)=>{
    return axios({
        method:'post',
        url:baseUrl+'/api/login',
        data:qs.stringify(data)
    })
}
// 购物车列表获取
export const requestCartList=(params)=>{
    return axios({
        method:'get',
        url:baseUrl+'/api/cartlist',
        params:params
    })
}
// 购物车商品添加
export const requestAddCart=(data)=>{
    return axios({
        method:'post',
        url:baseUrl+'/api/cartadd',
        data:data
    })
}



