<template>
<div>
  <van-nav-bar title="会员中心" left-arrow  @click-left="$router.go(-1)" />  
  <div class="touxiang">
      <van-image
  round
  width="10rem"
  height="10rem"
  src="https://img.yzcdn.cn/vant/cat.jpeg"
/>
  <div class="names">{{nickname}}</div>
  </div>
  <van-button type="danger" @click="changeName" block>安全退出</van-button>
  <div class="mian">
    <van-cell is-link title="我的收藏"  />
    <van-cell is-link title="我的评论"  />
    <van-cell is-link title="我的订单"  />
  </div>
</div>
  
</template>

<script>
export default {
  data(){
    return{
        nickname:'',
    }
  },
  methods:{
    changeName(){
      sessionStorage.removeItem('data')
      this.$toast.success('退出成功')
      this.$router.push('/login')
    }
  },
  mounted(){
    const user=JSON.parse(sessionStorage.getItem('data')||'{}')
   if(user){
      this.nickname=user.nickname
   }
  }
}
</script>

<style scoped>

.touxiang{
  width: 100%;
  height: 200px;
  justify-content: center;
  background: #cccccc;
  align-items: center;
  display: flex;
  flex-direction: column;
}
.van-button{
  margin: 20px auto;
  width: 90%;
}
.names{
  font-size: 20px;
  color: purple;
}
</style>


