<template>
  <div>
    <van-nav-bar
      right-text="登录"
      @click-right="$router.push('/login')"
      title="注册"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-model="data.phone"
        name="用户名"
        label="手机号"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="data.nickname"
        name="昵称"
        label="昵称"
        placeholder="昵称"
        :rules="[{ required: true, message: '请填写你的昵称' }]"
      />
      <van-field
        v-model="data.password"
        type="password"
        name="密码"
        label="设置密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="danger" native-type="submit"
          >注册</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import { requestReg } from "../utils/request";
export default {
  data() {
    return {
      data: {
        phone: "",
        nickname: "",
        password: "",
      },
    };
  },
  methods: {
    onSubmit() {
      requestReg(this.data).then((data) => {
        if (data.code == 200) {
          this.$toast.success(data.msg);
          this.$router.push("/login");
        }
      });
    },
  },
};
</script>

<style>
.van-form {
  margin-top: 20px;
}
</style>


