<template>
  <div>
    <van-nav-bar left-arrow @click-left="$router.go(-1)" title="商品列表" />
    <div v-if="goodslist!==null">
      <van-card
        v-for="item in goodslist"
        :key="item.id"
        :price="item.price"
        :title="item.goodsname"
        :thumb="$preImg + item.img"
      >
        <template #footer>
          <van-button
            type="primary"
            size="small"
            icon="cart-o"
          ></van-button> </template
      ></van-card>
    </div>
    <van-empty v-else description="暂无数据" />
  </div>
</template>

<script>
import { requestCateGoods } from "../utils/request";
export default {
  data() {
    return {
      goodslist: [],
    };
  },
  methods: {
    getgoodslist() {
      let fid = this.$route.query.fid;
      requestCateGoods({ fid: fid }).then((data) => {
        if (data.code == 200) {
          this.goodslist = data.list;
        }
      });
    },
  },

  mounted() {
    this.getgoodslist();
  },
};
</script>

<style>
</style>


