<template>
  <div class="indexs">
    <van-nav-bar title="首页" />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="red">
      <van-swipe-item v-for="i in bannerlist" :key="i.id">
        <img :src="$preImg + i.img" alt="" />
      </van-swipe-item>
    </van-swipe>

    <van-tabs v-model="active">
      <van-tab title="热门推荐">
        <van-card
          v-for="item in goodslist[0].content"
          :key="item.id"
          :price="item.market_price"
          :title="item.goodsname"
          tag="hot"
          :thumb="$preImg+item.img"
        >
          <template #footer>
            <van-button type="primary" size="small" icon="cart-o"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="新品上市">
        <van-card
          v-for="item in goodslist[1].content"
          :key="item.id"
          :price="item.market_price"
          :title="item.goodsname"
          tag="new"
          :thumb="$preImg+item.img"
        >
          <template #footer>
            <van-button type="primary" size="small" icon="cart-o"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="全部商品">
        <van-card
          v-for="item in goodslist[2].content"
          :key="item.id"
          :price="item.market_price"
          :title="item.goodsname"
          :thumb="$preImg+item.img"
        >
          <template #footer>
            <van-button type="primary" size="small" icon="cart-o"></van-button>
          </template>
        </van-card>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { requsetBanner, requsestIdexGoods } from "../utils/request";
export default {
  data() {
    return {
      active: 0,
      bannerlist: [],
      goodslist: [ {content: [] }, {content: [] }, {content: [] }],
    };
  },
  methods: {
    getBanner() {
      requsetBanner().then((data) => {
        if (data.code == 200) {
          this.bannerlist = data.list;
        }
      });
    },
    getIndexGoods() {
      requsestIdexGoods().then((data) => {
        if (data.code == 200) {
          this.goodslist = data.list;
        }
      });
    },
    // addCart(){
    //   requestAddCart(this.)
    // }
  },
  mounted() {
    this.getBanner();
    this.getIndexGoods();
  },
};
</script>

<style>
.van-tabs {
  margin-bottom: 40px;
}
/* .my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
} */
.van-swipe-item img {
  width: 100%;
  height: 100%;
}
</style>


