<template>
  <div>
    <van-nav-bar title="购物车" left-arrow @click-left="$router.go(-1)" />
    <van-swipe-cell>
      <van-card
        v-for="(item,index) in cartList"
        :key="item.id"
        :price="item.price"
        :title="item.goodsname"
        class="goods-card"
        :thumb="$preImg+item.img"
      >
        <template #footer>
          <van-stepper
            v-model="cartList[index].num"
            theme="round"
            button-size="22"
            disable-input
          /> </template
      ></van-card>

      <template #right>
        <van-button square text="删除" type="danger"  class="delete-button" />
      </template>
    </van-swipe-cell>

    <van-submit-bar :price="change" button-text="提交订单" />
  </div>
</template>

<script>
import { requestCartList } from "../utils/request";
export default {
  data() {
    return {
      cartList:[]
    };
  },
  computed:{
    change(){
      let sum= 0;
      this.cartList.map(item=>{
        sum+=item.num*item.price
      })
      return  sum*100
    }
  },
  mounted() {
    const user = JSON.parse(sessionStorage.getItem("data") || "{}");
    if (user.token) {
      const uid = user.uid;
      requestCartList({ uid: uid }).then((data) => {
        if (data.code == 200) {
          this.cartList=data.list
        }
      });
    }
  },
};
</script>

<style>
.van-swipe-cell {
  margin-bottom: 92px;
}
.van-submit-bar {
  margin-bottom: 50px;
}
.goods-card {
  margin: 0;
  background-color: white;
}

.delete-button {
  height: 100%;
}
</style>


